.resume_paper {
  position: relative;
  width: 150px;
  height: 200px;
  animation: cardRotation 12s linear infinite;
  transform-style: preserve-3d;
}

.resume_paper::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  background-image: url("@/assets/paper.webp");
  background-size: cover;
  border-radius: 6px;
}

.resume_paper::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  /* background-color: #e7eaf4; */
  background-image: linear-gradient(to bottom right, #e7eaf4, white);
  --neutral-15: ;
  transform: rotateY(180deg);
  backface-visibility: hidden;
  border-radius: 6px;
}

@keyframes cardRotation {
  from {
    transform: rotateY(0deg) translateY(-300px);
  }
  to {
    transform: rotateY(720deg) translateY(1200px);
  }
}
