.diy_or_tpl_box {
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
}
.diy_or_tpl_img {
  width: 56px;
  height: 56px;
  background: url(@/assets/sprite.svg) no-repeat;
  background-size: cover;
  cursor: pointer;
}
.diy_or_tpl_img1 {
  background-position: -280px 0;
}
.diy_or_tpl_img2 {
  background-position: -336px 0;
}

.diy_or_tpl_text {
  padding-top: 20px;
  color: #ccc;
}

.diy_or_tpl_img1:hover,
.diy_or_tpl_img2:hover {
  scale: 1.1;
}
